import React from 'react';
import style from "../common/styles/Nav.module.scss";
import {NavLink} from "react-router-dom";

const Nav: React.FC = () => {

    const setActive = ({isActive}: { isActive: boolean }): string => isActive ? style.active : style.item

    return (
        <div className={style.nav}>
            <NavLink className={setActive} to="/main"></NavLink>
            <NavLink className={setActive} to="/skills"></NavLink>
            <NavLink className={setActive} to="/projects"></NavLink>
            <NavLink className={setActive} to="/distanceJob"> </NavLink>
            <NavLink className={setActive} to="/contacts"></NavLink>
        </div>
    )
}

export default Nav;